/* AboutPage.css */

/* Style for the About Us header */
h2 {
    text-align: center;
    font-size: 24px;
  }
  
  /* Style for each product section */
  .product-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
    padding: 20px;
    background-color: #000000;
  }
  
  /* Style for each product card */
  .product-card {
    position: relative;
    width: 300px;
    height: 60%;
    border: 1px solid #ccc;
    background-color: #000000;
    color: white;
    padding: 12px;
    text-align: center;
    border-radius: 10px;
    overflow: hidden; /* Hide the image overflow to make hover effect work */
    transition: transform 0.3s ease; /* Add a smooth transition effect */
  }
  
  .product-card:hover {
    transform: scale(1.05); /* Scale up the card on hover */
  }
  .product-card p{
    font-size: 20px;
    font-family: 'poppins';
  }
  /* Style for the product image */
  .product-image {
    max-width: 80%;
    height: 60%;
    border-radius: 40px;
    transition: transform 0.3s ease; /* Add a smooth transition effect */
  }
  
  /* Style for the product image on hover */
  .product-card:hover .product-image {
    transform: scale(1.2); /* Scale up the image on hover */
  }
  
  
  /* Style for the Add to Cart button */
  .cart-button2{
    background-color: #000000;
    color: #fff;
    border: none;
    border-radius: 20px;
    padding: 10px 25px;
    cursor: pointer;
   position: absolute;
   right: 20px;
   top: 20px;
   font-size: 20px;
  }
   button {
    background-color: #000000;
    color: #fff;
    border: 2px solid white;
    border-radius: 20px;
    padding: 10px 25px;
    cursor: pointer;
 
  }
  
  /* Style for the cart section */
  .cart-section {
    text-align: center;
    margin-top: 20px;
  }
  
  .cart {
    border: 1px solid #ccc;
    padding: 80px;
    position: absolute;
    top: 90px;
    right: 10px;
    background-color: #000000;
    box-shadow: 0 0 5px rgba(250, 250, 250, 0.2);
    z-index: 1;
    color: #fff;
  }
  
  .cart h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .cart-item {
    display: flex;
    align-items: center;
    margin: 10px 0;
  }
  
  .cart-image {
    max-width: 50px;
    max-height: 50px;
    margin-right: 10px;
  }
  
  /* Style for the Order Now button */
  .cart button {
    background-color: #28a745;
    color: #fff;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    position: absolute;
    bottom: 0%;
  }
  
  /* Style for the order confirmation */
  .order-confirmation {
    text-align: center;
    margin-top: 20px;
  }
  
  .order-confirmation p {
    font-size: 18px;
  }
  
  /* Style for the Close button in the order confirmation */
  .order-confirmation button {
    background-color: #dc3545;
    color: #fff;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
  }
  
  /* Adjust other styles as needed */
  /* Styling for the added to cart popup */
.added-to-cart-popup {
    position: fixed;
    bottom: 20px; /* Adjust as needed to control the vertical position */
    right: 20px; /* Adjust as needed to control the horizontal position */
    background-color: #4CAF50; /* Background color */
    color: #fff; /* Text color */
    padding: 10px 20px; /* Padding */
    border-radius: 5px; /* Rounded corners */
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); /* Box shadow for a subtle effect */
    z-index: 9999; /* Ensure it's above other elements */
    animation: fadeInOut 2s ease-in-out; /* Add a fade-in and fade-out animation */
  }
  
  /* Keyframes for fade-in and fade-out animation */
  @keyframes fadeInOut {
    0% {
      opacity: 0;
    }
    10%, 90% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
